/**
 * 获取浏览器拍照权限
 */

/**
 * 获取浏览器权限
 * @param option
 */
function getCamera(option) {
    if (!option) {
        throw new Error('Options must be provided');
    }

    const { canvasId, videoId, videoWidth, videoHeight } = option;

    const canvas = document.getElementById(canvasId);
    const context = canvas.getContext('2d');
    const video = document.getElementById(videoId);
    video.style.display = 'block';

    if (!canvas || !context || !video) {
        throw new Error('Invalid DOM elements IDs');
    }

    // 设置媒体属性与兼容性处理
    if (navigator.mediaDevices === undefined) {
        navigator.mediaDevices = {};
    }

    if (navigator.mediaDevices.getUserMedia === undefined) {
        navigator.mediaDevices.getUserMedia = function (constraints) {
            const getUserMedia = navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia;
            if (!getUserMedia) {
                return Promise.reject(new Error('getUserMedia is not implemented in this browser'));
            }
            return new Promise((resolve, reject) => {
                getUserMedia.call(navigator, constraints, resolve, reject);
            });
        };
    }

    const constraints = {
        audio: false,
        video: {
            width: videoWidth,
            height: videoHeight,
            facingMode: "user", // 确保用户前置摄像头使用
        },
    };

    navigator.mediaDevices.getUserMedia(constraints)
        .then(function (stream) {
            if ('srcObject' in video) {
                video.srcObject = stream;
            } else {
                video.src = window.URL.createObjectURL(stream); // 已弃用但为了兼容性而保留
            }
            video.onloadedmetadata = function (e) {
                video.play();
            };
        })
        .catch((err) => {
            console.error('Error accessing camera: ', err);
            throw err;
        });

    option.thisCanvas = canvas;
    option.thisContext = context;
    option.thisVideo = video;

    return option;
}

/**
 * 绘制图片并返回base64
 * @param option
 */
function draw(option) {
    const { thisContext, thisVideo, videoWidth, videoHeight } = option;
    if (!thisContext || !thisVideo) {
        throw new Error('Invalid video or context');
    }

    thisContext.drawImage(thisVideo, 0, 0, videoWidth, videoHeight);
    return thisCanvas.toDataURL('image/png');
}

export default {
    getCamera,
    draw,
};